/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import "../../node_modules/swiper/swiper-bundle.css";
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
  transition: all 0.2s linear;
}

html {
  font-family: "Roboto", sans-serif;
  height: 100%;
  font-size: 62.5%;
  overflow-x: hidden;
}
html::-webkit-scrollbar {
  width: 1rem;
}
html::-webkit-scrollbar-track {
  background: transparent;
}
html::-webkit-scrollbar-thumb {
  background: #d5d9dd;
}
@media (max-width: 108em) {
  html {
    font-size: 9px;
  }
}
@media (max-width: 105em) {
  html {
    font-size: 8.75px;
  }
}
@media (max-width: 91.5em) {
  html {
    font-size: 7.9px;
  }
}
@media (max-width: 91em) {
  html {
    font-size: 8.4px;
  }
}
@media (max-width: 90em) {
  html {
    font-size: 7.9px;
  }
}
@media (max-width: 80em) {
  html {
    font-size: 7.2px;
  }
}
@media (max-width: 72em) {
  html {
    font-size: 6.5px;
  }
}
@media (max-width: 64em) {
  html {
    font-size: 5.5px;
  }
}
@media (max-width: 48em) {
  html {
    font-size: 10px;
    -webkit-text-size-adjust: none;
  }
}
@media (max-width: 44em) {
  html {
    font-size: 9px;
  }
}
@media (max-width: 38em) {
  html {
    font-size: 8px;
  }
}
@media (max-width: 34em) {
  html {
    font-size: 7px;
  }
}
@media (max-width: 30em) {
  html {
    font-size: 6px;
  }
}
@media (max-width: 26em) {
  html {
    font-size: 4.5px;
  }
}
@media (max-width: 21em) {
  html {
    font-size: 4.4px;
  }
}

html._hidden {
  overflow: hidden;
}

body {
  display: flex;
  position: relative;
  flex-direction: column;
  min-height: 100%;
  background-color: #F6F8F9;
  color: #373535;
}

header {
  flex: 0 0 auto;
}

main {
  flex: 0 0 auto;
  padding-top: 7rem;
}

footer {
  flex: 0 0 auto;
}

.container {
  max-width: 120rem;
  width: 100%;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.hidden {
  display: none;
}

.flex {
  display: flex;
}

.bg-body {
  background: #fff;
}

@font-face {
  font-family: "Roboto-Bold";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto-Light";
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../Roboto-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto-Regular";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-image: url(../img/flag/russia_3.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: 10000;
}
@media (max-width: 767px) {
  .header {
    background-image: none;
  }
}

.nav {
  position: relative;
}
@media (max-width: 767px) {
  .nav {
    overflow: auto;
    height: 100vh;
  }
}
.nav__header {
  display: none;
  width: 100%;
  height: 7rem;
  padding: 5rem 2rem;
  background-color: #f0f0f0;
  box-shadow: 0 0 10px 5px rgba(116, 110, 78, 0.5);
}
@media (max-width: 767px) {
  .nav__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.nav__title {
  font-size: 4rem;
  text-transform: uppercase;
}
.nav__burger {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: transparent;
  outline: none;
  border: 0.2rem solid rgba(116, 110, 78, 0.5);
  border-radius: 0.5rem;
}
.nav__burger:hover {
  border: 0.2rem solid #373535;
}
.nav__burger:hover .nav__icon-burger {
  stroke: #373535;
  rotate: 180deg;
}
.nav__burger:hover .nav__icon-close {
  fill: #373535;
  rotate: 180deg;
}
.nav__icon-burger, .nav__icon-close {
  width: 5rem;
  height: 5rem;
  stroke: rgba(116, 110, 78, 0.5);
  stroke-width: 3;
  stroke-linecap: round;
}
.nav__icon-close {
  fill: rgba(116, 110, 78, 0.5);
}
.nav__icon-close._hidden, .nav__icon-burger._hidden {
  display: none;
}
@media (min-width: 767px) {
  .nav__menu {
    display: flex;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .nav__menu {
    display: none;
    background-color: #F6F8F9;
    min-height: 100vh;
    z-index: 10;
  }
}
.nav__menu._active {
  display: block;
}
.nav__item {
  margin: 0 auto;
  z-index: 10000;
}
.nav__dropdown {
  display: flex;
  font-size: 2.4rem;
  color: #373535;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  padding: 2rem;
  height: 100%;
}
.nav__dropdown:hover {
  color: #F6F8F9;
}
.nav__dropdown:hover html {
  overflow: hidden;
}
.nav__dropdown:hover body {
  overflow: hidden;
}
.nav__dropdown:hover .first__icon {
  fill: #F6F8F9;
  stroke: #F6F8F9;
}
.nav__dropdown:hover .arrow__icon {
  fill: #F6F8F9;
}
.nav__dropdown:focus {
  outline: none;
}

.submenu {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  transform: translate(-200%, -200%);
  pointer-events: none;
  overflow-y: auto;
  overflow-x: hidden;
  top: 6.4rem;
  background-color: #F6F8F9;
  width: 70vw;
  height: 70vh;
  z-index: 1000;
  padding: 0 3rem;
  box-shadow: inset 0 0 10px 5px rgba(0, 0, 0, 0.4);
  left: 50%;
  transform: translate(-50%);
}
@media (max-width: 767px) {
  .submenu {
    position: relative;
    top: 0;
    display: none;
  }
}
.submenu__title {
  font-size: 4rem;
}
.submenu__list--title {
  font-weight: 700;
}
.submenu__header {
  padding: 2rem 0;
  margin-bottom: 5rem;
  border-bottom: 0.1rem solid rgba(116, 110, 78, 0.5);
}
@media (max-width: 767px) {
  .submenu__header {
    display: none;
  }
}
.submenu__box {
  display: flex;
  gap: 3rem;
}
@media (max-width: 767px) {
  .submenu__box {
    flex-direction: column;
    margin-top: 2rem;
    gap: 1rem;
  }
}
.submenu__list-item {
  font-size: 2.4rem;
  font-weight: 700;
  margin-bottom: 2rem;
}
.submenu__link {
  font-size: 1.8rem;
  font-weight: 700;
  text-decoration: none;
  color: #373535;
}
.submenu__link:hover {
  color: #a94442;
  text-decoration: underline;
}

.first {
  width: 1.8rem;
  height: 1.8rem;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}
.first__icon {
  width: 1.8rem;
  height: 1.8rem;
  fill: #373535;
  stroke: #373535;
  stroke-width: 1;
}

.arrow {
  display: none;
  width: 3rem;
  height: 3rem;
}

.arrow__icon {
  width: 3rem;
  height: 3rem;
  fill: #000;
}

@media (min-width: 767px) {
  body._pc .nav__item:hover > .submenu {
    opacity: 1;
    visibility: visible;
    transform: translate(-50%);
    pointer-events: all;
  }
}

body._pc .nav__item > .submenu._active {
  display: block;
  opacity: 1;
  visibility: visible;
  transform: translate(-50%);
  pointer-events: all;
}

body._touch .submenu {
  opacity: 0;
  visibility: hidden;
  transform: translate(-200%);
  pointer-events: none;
}
@media (min-width: 767px) {
  body._touch .submenu {
    top: 7rem;
  }
}

body._touch .submenu._active {
  display: block;
  opacity: 1;
  visibility: visible;
  transform: translate(-50%);
  pointer-events: all;
}

body._touch .arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1rem;
}

body._touch .nav__dropdown {
  display: flex;
  align-items: center;
}

body._touch .arrow {
  margin-right: 0;
  margin-left: auto;
}

body._touch ._arrow-rotate {
  transform: rotate(-180deg);
}

.catalog-main-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 5rem 0;
  gap: 5rem;
}

.menu-center {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 25rem;
  height: 25rem;
  border-radius: 5rem;
  z-index: 10;
}
.menu-center__link {
  display: block;
  height: 100%;
  border-radius: 5rem;
}
.menu-center__link:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px 5px #373535;
  transform: rotate(5deg);
}
.menu-center__image {
  height: 100%;
}
.menu-center__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5rem;
}
.menu-center__title {
  display: block;
  position: absolute;
  font-weight: 700;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 4rem;
  padding: 2rem;
  background-color: #373535;
  text-decoration: none;
  color: #F6F8F9;
}

.breadscrumb-box {
  width: 100%;
  background-color: #abb8c3;
  border-radius: 5rem;
}

.breadscrumb-box--mt {
  margin-top: 2rem;
}
@media (max-width: 767px) {
  .breadscrumb-box--mt {
    margin-top: 6rem;
  }
}

.breadscrumb {
  display: flex;
  padding: 1rem 2rem;
}
.breadscrumb__item {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 0 1rem;
}
.breadscrumb__item::after {
  content: "/";
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(-50%, -50%);
  font-size: 1.6rem;
  font-weight: 700;
  color: #373535;
}
.breadscrumb__item:last-child::after {
  content: "";
}
.breadscrumb__link {
  text-decoration: none;
}
.breadscrumb__link:hover {
  opacity: 0.7;
}
.breadscrumb__link, .breadscrumb__text {
  display: block;
  font-size: 1.6rem;
  font-weight: 700;
  color: #373535;
}
.breadscrumb__icon {
  width: 1.6rem;
  height: 1.6rem;
  fill: #373535;
}

.row {
  width: 100%;
}
.row__title {
  font-size: 4rem;
  text-align: center;
  margin: 2rem 0;
  color: #373535;
}

.menu-category__link {
  display: flex;
  flex: 0 0 19rem;
  text-decoration: none;
  color: #F6F8F9;
  z-index: 50;
  border-radius: 0.5rem;
}
.menu-category__link:hover {
  box-shadow: 0px 0px 5px 5px #373535;
}
.menu-category__list {
  display: flex;
  gap: 3rem;
  justify-content: center;
  flex-wrap: wrap;
}
.menu-category__item {
  display: flex;
  flex: 0 0 19rem;
  height: 8rem;
  border: 0.1rem solid #000;
  z-index: 5;
  border-radius: 0.5rem;
}
.menu-category__image {
  flex: 0 0 6rem;
  height: 100%;
  position: relative;
  background-color: transparent;
}
.menu-category__img {
  position: absolute;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
  width: 7rem;
  height: auto;
  border-radius: 50%;
}
.menu-category__content {
  position: relative;
  flex: 0 0 13rem;
  height: 100%;
  background: url(../img/flag/russia_1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-right: 0.1rem solid #373535;
  border-top-right-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  z-index: -1;
}
.menu-category__title {
  position: absolute;
  font-size: 1.8rem;
  text-align: center;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.shadow {
  box-shadow: 0px 0px 5px 5px #373535;
}

.sidebar {
  flex: 0 0 27rem;
}
.sidebar__title {
  font-size: 2rem;
  margin-bottom: 2rem;
}
.sidebar__title--mt {
  margin-top: 2rem;
}

.content {
  flex: 0 0 90rem;
}

.menu-sidebar,
.menu-sb-main {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.menu-sidebar__item,
.menu-sb-main__item {
  width: 20rem;
  padding: 1.5rem 1rem;
  border: 0.1rem solid #373535;
  border-radius: 0.5rem;
}
.menu-sidebar__item:hover,
.menu-sb-main__item:hover {
  background-color: rgba(52, 191, 73, 0.2);
  box-shadow: 0 0 1rem 0.2rem #34bf49;
}
.menu-sidebar__dropdown,
.menu-sb-main__dropdown {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}
.menu-sidebar__dropdown span,
.menu-sb-main__dropdown span {
  font-size: 1.8rem;
  font-weight: 700;
}
.menu-sidebar__icon,
.menu-sb-main__icon {
  width: 2rem;
  height: 2rem;
  fill: rgba(79, 83, 88, 0.5);
}
.menu-sidebar__icon._rotate,
.menu-sb-main__icon._rotate {
  transform: rotate(180deg);
}
.menu-sidebar__submenu,
.menu-sb-main__submenu {
  position: relative;
  display: none;
  justify-content: space-between;
  gap: 0.5rem;
  flex-wrap: wrap;
  padding: 1rem;
  border: 0.1rem solid #373535;
  width: 20rem;
}
.menu-sidebar__submenu:after,
.menu-sb-main__submenu:after {
  content: "";
  position: absolute;
  top: -1.5%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0.3rem;
  height: 1.1rem;
  background-color: #373535;
}
.menu-sidebar__submenu._active,
.menu-sb-main__submenu._active {
  display: flex;
  background-color: #F6F8F9;
}
.menu-sidebar .menu-sidebar__submenu--fd,
.menu-sb-main .menu-sidebar__submenu--fd {
  flex-direction: column;
}
.menu-sidebar__submenu-label,
.menu-sb-main__submenu-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
  cursor: pointer;
}
.menu-sidebar__submenu-label--align-vertical,
.menu-sb-main__submenu-label--align-vertical {
  align-items: flex-start;
}
.menu-sidebar__submenu-fake,
.menu-sb-main__submenu-fake {
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  opacity: 0;
}
.menu-sidebar__submenu-fake:checked + .menu-sidebar__submenu-true::after,
.menu-sidebar__submenu-fake:checked + .menu-sb-main__submenu-true::after,
.menu-sb-main__submenu-fake:checked + .menu-sidebar__submenu-true::after,
.menu-sb-main__submenu-fake:checked + .menu-sb-main__submenu-true::after {
  opacity: 1;
  visibility: visible;
  pointer-events: visible;
}
.menu-sidebar__submenu-true,
.menu-sb-main__submenu-true {
  position: relative;
  display: block;
  width: 1.5rem;
  height: 1.5rem;
  border: 0.1rem solid #373535;
  border-radius: 0.3rem;
}
.menu-sidebar__submenu-true:after,
.menu-sb-main__submenu-true:after {
  content: "";
  position: absolute;
  top: 60%;
  left: 60%;
  transform: translate(-50%, -50%);
  width: 2rem;
  height: 2rem;
  background-image: url(../img/sprite.svg#checkmark_round_icon);
  background-repeat: no-repeat;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.menu-sidebar__submenu-year, .menu-sidebar__submenu-series,
.menu-sb-main__submenu-year,
.menu-sb-main__submenu-series {
  font-size: 1.4rem;
  font-weight: 700;
}
.menu-sidebar__submenu-count,
.menu-sb-main__submenu-count {
  font-size: 1.3rem;
  padding: 0.2rem 0.5rem;
  background-color: #abb8c3;
  border-radius: 0.5rem;
}

.menu-sb-main__item._bg {
  background-color: rgba(52, 191, 73, 0.2);
}
.menu-sb-main__category, .menu-sb-main__subcategory {
  position: relative;
  display: none;
  flex-direction: column;
  gap: 1rem;
}
.menu-sb-main__category._active, .menu-sb-main__subcategory._active {
  display: flex;
}
.menu-sb-main__category-item, .menu-sb-main__subcategory-item {
  position: relative;
  width: 20rem;
  padding: 1.5rem 1rem;
  border: 0.1rem solid #373535;
  border-radius: 0.5rem;
}
.menu-sb-main__category-item:after, .menu-sb-main__subcategory-item:after {
  content: "";
  position: absolute;
  top: -10%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0.3rem;
  height: 1.1rem;
  background-color: #373535;
}
.menu-sb-main__category-item:hover, .menu-sb-main__subcategory-item:hover {
  background-color: rgba(52, 191, 73, 0.2);
  box-shadow: 0 0 1rem 0.2rem #34bf49;
}
.menu-sb-main__subcategory-item {
  border: 0.1rem solid rgba(55, 53, 53, 0.7);
}
.menu-sb-main__subcategory-item:after {
  content: "";
  position: absolute;
  top: -10%;
  left: 10%;
  transform: translate(-50%, -50%);
  width: 0.3rem;
  height: 1.1rem;
  background-color: #373535;
}
.menu-sb-main__subcategory-item::before {
  content: "";
  position: absolute;
  top: -10%;
  left: 90%;
  transform: translate(-50%, -50%);
  width: 0.3rem;
  height: 1.1rem;
  background-color: #373535;
}
.menu-sb-main__category-dropdown, .menu-sb-main__subcategory-dropdown {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}
.menu-sb-main__category-dropdown._bg {
  background-color: rgba(52, 191, 73, 0.2);
}
.menu-sb-main__category-dropdown span, .menu-sb-main__subcategory-dropdown span, .menu-sb-main__subcategory-link, .menu-sb-main__category-link {
  font-size: 1.8rem;
}
.menu-sb-main__subcategory-dropdown span, .menu-sb-main__subcategory-link span, .menu-sb-main__category-link span {
  color: rgba(55, 53, 53, 0.7);
}
.menu-sb-main__subcategory-link, .menu-sb-main__category-link {
  display: block;
  text-decoration: none;
  width: 100%;
  height: 100%;
}
.menu-sb-main__category-icon, .menu-sb-main__subcategory-icon {
  width: 2rem;
  height: 2rem;
  fill: rgba(79, 83, 88, 0.5);
}
.menu-sb-main__category-icon._rotate {
  transform: rotate(180deg);
}

.btn-close {
  border: none;
  background: transparent;
}

.main-title {
  font-size: 6rem;
  text-transform: uppercase;
  text-align: center;
}

.main-ttitle--mt {
  margin-top: 5rem;
}

.sorting {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem;
  z-index: 50;
}
.sorting__label {
  position: relative;
  z-index: 50;
}
.sorting__box--gap {
